import back from '../../components/back.vue';
export default {
	data() {
		return {
			pageNameFlag:1,
			showFlag:0,
			pageName:'异常终端列表',
			radioFlag:0,
			devMsgIndex:'',
			deviceList:[],
			page:1,
			showTime:'',
			errorFlag:0
		}
	},
	methods: {
		queryDeviceList(){
			var that = this;
			uni.request({
				url: that.serverUrl+'/deviceMsg/deviceList',
				method: 'POST',
				data: {
					devMsgIndex: that.devMsgIndex,
					page:that.page++,
					num:20
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				sslVerify: false,
				success: (res) => {
					uni.hideLoading();
					if (res.data.resultCode == '10000') {
						var list = eval(res.data.data)
						if(list!=null&&list!=undefined&&list!=''&&list!=[]){
							that.deviceList = that.deviceList.concat(list);
						}
						
					} else {
						that.errorFlag = 1
					}
				},
				fail: (re) => {
					that.errorFlag = 1
				}
			});
		},
		jumpToTerminalDetails(timeID,devIndex){
			var that = this;
			uni.navigateTo({
				url:'../terminalDetails/terminalDetails?devIndex='+devIndex+"&timeID="+timeID+"&showTime="+that.showTime
			})
		},
		retry(){
			this.errorFlag=0;
			this.page=1;
			this.queryDeviceList();
		}
	},
	onLoad(e) {
		this.devMsgIndex = e.devMsgIndex;
		this.showTime=e.showTime;
		this.queryDeviceList();
	},
	onReachBottom(){
		this.queryDeviceList();
	},
	components:{
		back
	}
}
